body{
  font-family: "Expletus Sans", sans-serif;
  font-optical-sizing: auto;
 
  font-style: normal;
}

.Nav_Container {
  display: flex;
 gap:5%;
  align-items: center;
  background-color: black;
  padding: 15px;
}

.Navbar_Toggle {
  display: none;
  cursor: pointer;
  color: white;
  margin-left: 20px;
}

.Navbar_Toggle i {
  color: white;
  font-size: 24px;
}
li:hover {
  
  /* background: #e1e1e1; */
  border-top: 3px solid #FFD700;
  border-radius: 2px;
 
}

.Navbar {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin: 0px 0px 0px 100px;
  padding: 0;
  gap: 30px;
  border-top: 3px solid none;
}

.Navbar li {
  margin-right: 20px;
 
}

.Navbar li:last-child {
  margin-right: 0;
}

.Navbar a {
  color: white;
  text-decoration: none;
}

