.logo{
  width: 50%;
}

.Nav_logo {
  width: 60px;
  margin-left: 30%; /* Initial position */
}

@media only screen and (max-width: 768px) {
  .Navbar {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    background-color: black;
    padding: 10px;
    z-index: 1;
  }

  .Navbar.open {
    display: flex;
  }

  .Navbar li {
    margin-bottom: 10px;
    margin-left: 10px;
  }

  .Navbar_btn {
    display: flex;
  }

  .Navbar_Toggle {
    display: block;
  }
  .logo{
    width: 100%;
  }

  .Nav_logo {
    width: 60px;
    margin-left: 60%;
 
  }
}
